#modal {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 15%;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 100%;
  }

  #modal-background {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    background-color: rgb(0 0 0 / 18%);
  }

  #modal-content {
    position: absolute;
    display: flex;
    border-radius: 3px;
    background-color: white;
    height: 100%;
    width: 400px;
    flex-direction: column;
    z-index: 10;
    overflow: scroll;
  }
