.search-container {
    display: flex;
    justify-content: center;
    border: 1px solid #e6e6e6;
    padding: 10px 25px 10px 15px;
    border-radius: 20px;
    font-size: 15px;
    margin-top: 80px;
    position: absolute;
    background-color: white;
}

.search-div {
    display: flex;
    align-items: center;
}

#search {
    display: flex;
    width: 240px;
    border: none;
}

#search-img {
    display: flex;
    height: 18px;
    padding-right: 10px;
}

#visit-your-profile-btn {
    border: 1px solid rgba(25, 25, 25, 1);
    color: white;
    padding: 10px 108px;
    border-radius: 20px;
    font-size: 14px;
    background-color: rgba(25, 25, 25, 1);
    cursor: pointer;
}

.asfd {
    display: flex;
    position: absolute;
    top: 55px;
    right: 2px;
    flex-direction: column;
}

.search-results-container {
    display: flex;
    border: 1px solid #9999993b;
    height: 47px;
    border-radius: 5px;
    box-shadow: 2px 2px 20px 1px rgb(135 135 135 / 25%);
    padding: 8px 24px;
    width: 260px;
    background-color: white;
    flex-direction: column;
    justify-content: center;
}

.search-result-div {
    display: flex;
    flex-direction: row;
    align-items: center;
    height: 35px;
}

.search-imgs-div {
    display: flex;
}

.search-result {
    display: flex;
    width: fit-content;
    color: black;
    font-size: 14px;
}

.story-photo {
    height: 23px;
    width: 23px;
    object-fit: cover;
    border-radius: 50%;
    margin-right: 8px;
}
